.signin{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.signin>h1{
  margin-top: 20%;
}

.form{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
}

input#name, input#email, input#password{
  width: 60%;
  margin: .5rem;
  padding: .5rem;
  border: 1px solid rgba(5, 255, 85, 1);
  border-radius: 3px;
  color: rgb(5, 255, 85);
  font-size: 14px;
  background: transparent;
}

input#send{
  width: 150px;
  margin-top: .5rem;
  border-radius: 3px;
  padding: .5rem;
  color: white;
  background: rgba(5,255,85,0);
  border: solid 1px rgb(5,255,85);
  font-size: 14px;
  cursor: pointer;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

input#send:hover{
  background: rgba(5,255,85,.6);
}



