.todolist {
display: -ms-flexbox;
display: flex;
-ms-flex-pack: start;
    justify-content: flex-start;
-ms-flex-direction: column;
    flex-direction: column;
-ms-flex-align: center;
    align-items: center;
min-height: 60vh;
}

.todoitem {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 81%;
    padding: .5rem  ;
    border-bottom: 1px solid rgba(5, 225, 85, 0.1);
}

.todoitem>p{
  padding: 0 1rem;
  color: white !important;
}

.todoitem:last-child{
  border-bottom: none;
}

.markAsDone{
  text-decoration: line-through;
}

.buttons-todo{
  display: -ms-flexbox;
  display: flex;
  margin: 0 .2rem;
  font-size: 12px;
  height: 35px;
}

.sucess{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: .3rem;
  color: rgb(0, 225, 255);;
  background: transparent;
  border-radius: 2px;
  min-width: 35px;
  min-height: 35px;
  background-image: url('../assets/check.svg');
  background-size: 15px;
  background-repeat: no-repeat;
  background-position: center;
}

.sucess:hover{
  font-size: 23px;
}

.pending{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: rgb(0, 225, 255);;
  background: transparent;
  border-radius: 2px;
  padding: .3rem;
  min-width: 35px;
  min-height: 35px;
  background-image: url('../assets/reload.svg');
  background-size: 15px;
  background-repeat: no-repeat;
  background-position: center;
}

.pending:hover{
  font-size: 23px;
}

.delete{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: .3rem;
  color: rgb(0, 225, 255);;
  background: transparent;
  border-radius: 2px;
  min-width: 35px;
  min-height: 35px;
  background-image: url('../assets/wrong.svg');
  background-size: 15px;
  background-repeat: no-repeat;
  background-position: center;
}

.delete:hover{
  font-size: 23px;
}

@media screen and (max-width: 414px) {
  .todoitem>p{
    padding: 0;
    font-size: 14px;
  }
}




