.todoForm {
display: -ms-flexbox;
display: flex;
-ms-flex-pack: center;
    justify-content: center;
}

input#description {
  background: transparent;
  border: 1px solid rgb(5, 255, 85);
  border-radius: 3px;
  padding: .5rem;
  width: 80%;
  color: rgb(5, 255, 85);
  font-size: 14px;
}
.buttons{
  display: -ms-flexbox;
  display: flex;
}
.buttons>button {
  padding: .6rem;
  background-image: -webkit-gradient(linear, left top, right top, from(rgb(26, 158, 175)), to(rgb(0, 225, 255)));
  background-image: -webkit-linear-gradient(left, rgb(26, 158, 175) 0%, rgb(0, 225, 255) 100%);
  background-image: -o-linear-gradient(left, rgb(26, 158, 175) 0%, rgb(0, 225, 255) 100%);
  background-image: linear-gradient(to right, rgb(26, 158, 175) 0%, rgb(0, 225, 255) 100%);
  color: white;
  margin-left: .5rem;
  width: 120px;
  border-radius: 3px;
}



