*{
  padding: 0;
  border: 0;
  margin: 0;
}

@import url('https://fonts.googleapis.com/css?family=Roboto:100,300');

body::-webkit-scrollbar-track { border-radius: 3px; background-color: #141414 ; }

body::-webkit-scrollbar { width: 8px; background-color: #141414 ; }

body::-webkit-scrollbar-thumb { border-radius: 2px; background-color: rgb(5, 225, 85); }

body{
  font-family: Roboto;
  font-weight: 100;
  background-image: -webkit-linear-gradient(315deg, #000000 0%, #030a06 100%);
  background-image: -o-linear-gradient(315deg, #000000 0%, #030a06 100%);
  background-image: linear-gradient(-225deg, #000000 0%, #030a06 100%);
  height: 100%;
  width: 100%;
  min-height: 100vh;
}



